
import { Component, Vue } from "vue-property-decorator";
import { State, Getter, Action, Mutation, namespace } from "vuex-class";

@Component
export default class extends Vue {
  created() {}
  @Getter("words") words: any;
  @State("dictionary") dictionary: any;

  get longInfoList() {
    return [
      {
        icon: ["fas", "home"],
        msg: this.words.address,
      },
    ];
  }

  get shortInfoList() {
    return [
      {
        icon: ["fas", "envelope"],
        msg: this.words.email,
      },
      {
        icon: ["fas", "phone-alt"],
        msg: this.words.phone,
      },
      {
        icon: ["fas", "tv"],
        msg: this.words.web,
      },
    ];
  }

  setColor(item: any) {
    item.hovTheme = item.theme;
  }

  resetColor(item: any) {
    item.hovTheme = "#000";
    item.bgC = "#fff";
  }

  icons = [
    {
      hovTheme: "#000",
      icon: ["fab", "github"],
      href: "https://github.com/lin-nie",
      theme: "lightseagreen",
    },
    {
      hovTheme: "#000",
      icon: ["fab", "linkedin"],
      href: "https://www.linkedin.com/in/nie-lin/",
      theme: "rgb(24, 119, 242)",
    },
    {
      hovTheme: "#000",
      icon: ["fab", "facebook-square"],
      href: "https://www.facebook.com/profile.php?id=100028198980924",
      theme: "rgb(59, 87, 157)",
    },
    {
      hovTheme: "#000",
      icon: ["fab", "twitter-square"],
      href: "https://twitter.com/NieLin6",
      theme: "rgb(59, 200, 244)",
    },
  ];
}
